import React from  'react';
import {
  BrowserRouter as Router,
  Route,Routes} from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import Navbar from "./component/navbar.component";
import ExerciseList from "./component/exercise-list.component";
import EditExercise from "./component/edit-exercise.component";
import CreateExercise from "./component/create-exericse.component";
import CreateUser from "./component/create-user.component";

function App() {
  return (
    <Router>
      <div className="container">
      <Navbar />
      <br/>
       <Routes>
       <Route exact path='/' element={<ExerciseList/>}/>
       <Route  path='/edit/:id' element={<EditExercise/>}/>
       <Route  path='/create' element={<CreateExercise/>}/>
       <Route  path='/user' element={<CreateUser/>}/>
       </Routes>
       </div>
     </Router>
  );
}

export default App;
